"use client";

import { useRef } from "react";
import { useRouter } from "next/navigation";
import { encodeId } from "../../../lib/utils/secureId";

import {
  DATEICON,
  LOCATION_ICON,
  MODE_ICON,
  SAVEICON,
  TICKET_ICON,
  VIEW_ICON,
} from "../../../const-value/config-icons/page";
import "./EventSlider.module.css"

export default function EventSlider({ title, data = [] }) {
  const router = useRouter();
  const sliderRef = useRef(null);

  const slideLeft = () => {
    sliderRef.current?.scrollBy({ left: -350, behavior: "smooth" });
  };

  const slideRight = () => {
    sliderRef.current?.scrollBy({ left: 350, behavior: "smooth" });
  };

  const handleClick = (eventId) => {
    router.push(`/event/${encodeId(eventId)}`);
  };

  return (
    <section className="container-fluid mt-4">
      {/* HEADER */}
      <div className="d-flex justify-content-between align-items-center mb-2">
        <h5 className="fw-bold mb-0">{title}</h5>
        <button className="btn btn-outline-dark rounded-pill px-4">
          See all
        </button>
      </div>

      <hr />

      {/* NAV BUTTONS */}
      <div className="d-flex justify-content-end gap-2 mb-2">
        <button className="btn btn-light rounded-circle" onClick={slideLeft}>
          ❮
        </button>
        <button className="btn btn-light rounded-circle" onClick={slideRight}>
          ❯
        </button>
      </div>

      {/* EMPTY STATE */}
      {!Array.isArray(data) || data.length === 0 ? (
        <p className="text-center text-muted py-4">No events found</p>
      ) : (
        <div
          className="d-flex gap-3 overflow-hidden"
          ref={sliderRef}
          style={{ scrollBehavior: "smooth" }}
        >
          {data.map((event, index) => (
            <div
              key={event.identity ?? index}
              className="card flex-shrink-0"
              style={{ width: "315px", cursor: "pointer" }}
              onClick={() => handleClick(event.identity)}
            >
              <img
                src={
                  event.bannerImage ||
                  "https://cloudinary-marketing-res.cloudinary.com/images/w_1000,c_scale/v1679921049/Image_URL_header/Image_URL_header-png?_i=AA"
                }
                className="card-img-top"
                style={{ height: "120px", objectFit: "cover" }}
                alt="Event"
              />

              <div className="card-body p-3">
                {/* TITLE */}
                <div className="d-flex justify-content-between align-items-center mb-1">
                  <span
                    className="fw-semibold text-truncate"
                    style={{ maxWidth: "220px" }}
                  >
                    {event.title || "Untitled Event"}
                  </span>
                  {SAVEICON}
                </div>

                {/* DETAILS */}
                <div className="small text-muted">
                  <div className="d-flex justify-content-between">
                    <span className="text-truncate" style={{ maxWidth: "140px" }}>
                      {LOCATION_ICON} {event.org?.city || "N/A"}
                    </span>
                    <span>{TICKET_ICON} Paid</span>
                  </div>

                  <div className="d-flex justify-content-between">
                    <span className="text-truncate" style={{ maxWidth: "140px" }}>
                      {DATEICON} {event.eventDate || "TBA"}
                    </span>
                    <span>{MODE_ICON} {event.mode}</span>
                  </div>
                </div>

                {/* FOOTER */}
                <div className="d-flex justify-content-between align-items-center mt-3">
                  <span className="badge bg-light text-dark px-3 py-2">
                    {VIEW_ICON} 456
                  </span>
                  <button className="btn btn-sm btn-outline-primary rounded-pill px-4">
                    View
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}
    </section>
  );
}
