"use client";

import { useEffect, useState } from "react";
import styles from "./SpotlightCarousel.module.css";

import {
  DATEICON,
  LOCATION_ICON,
  SHAREICON,
} from "../../../const-value/config-icons/page";

/* ---------------- SAFE DATE FORMATTER ---------------- */
function formatDate(dateString) {
  const d = new Date(dateString);
  return d.toISOString().replace("T", " ").slice(0, 16);
}

/* ---------------- DEFAULT DATA ---------------- */
const defaultSlides = [
  {
    id: 1,
    img: "/images/event.png",
    title: "Quick Solvathon Innovation Challenge",
    location: "KCT Tech Park, Coimbatore",
    datetime: "2025-10-19T17:00:00",
  },
  {
    id: 2,
    img: "/images/event.png",
    title: "International Conference on Design",
    location: "Coimbatore Convention Center",
    datetime: "2025-11-18T10:00:00",
  },
];

/* ---------------- COUNTDOWN HOOK ---------------- */
function useCountdown(targetIso) {
  const [diff, setDiff] = useState(() =>
    Math.max(new Date(targetIso) - new Date(), 0)
  );

  useEffect(() => {
    const timer = setInterval(() => {
      setDiff(Math.max(new Date(targetIso) - new Date(), 0));
    }, 1000);
    return () => clearInterval(timer);
  }, [targetIso]);

  const secs = Math.floor(diff / 1000) % 60;
  const mins = Math.floor(diff / (1000 * 60)) % 60;
  const hours = Math.floor(diff / (1000 * 60 * 60)) % 24;
  const days = Math.floor(diff / (1000 * 60 * 60 * 24));

  return { days, hours, mins, secs };
}

/* ---------------- COMPONENT ---------------- */
export default function SpotlightCarousel({ slides = defaultSlides }) {
  const [current, setCurrent] = useState(0);
  const total = slides.length;

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrent((prev) => (prev + 1) % total);
    }, 8000);
    return () => clearInterval(timer);
  }, [total]);

  const goto = (i) => setCurrent((i + total) % total);
  const prev = () => goto(current - 1);
  const next = () => goto(current + 1);

  return (
    <section className={styles.root}>
      <h2 className={styles.title}>Top Spotlights</h2>

      <div className={styles.wrapper}>
        <div
          className={styles.track}
          style={{ transform: `translateX(-${current * 100}%)` }}
        >
          {slides.map((s) => {
            const { days, hours, mins, secs } = useCountdown(s.datetime);

            return (
              <article className={styles.slide} key={s.id}>
                <div className={styles.left}>
                  <img src={s.img} alt={s.title} className={styles.image} />
                </div>

                <div className={styles.right}>
                  <div className={styles.header}>
                    <h3>{s.title}</h3>
                    <button className={styles.share}>{SHAREICON}</button>
                  </div>

                  <div className={styles.meta}>
                    <p>{LOCATION_ICON} {s.location}</p>
                    <p>{DATEICON} {formatDate(s.datetime)}</p>
                  </div>

                  <div className={styles.startsIn}>Event Starts In</div>

                  <div className={styles.countdown}>
                    {[days, hours, mins, secs].map((v, i) => (
                      <div key={i} className={styles.pill}>
                        <div className={styles.num}>{String(v).padStart(2, "0")}</div>
                      </div>
                    ))}
                    <button className={styles.cta}>Get Ticket</button>
                  </div>
                </div>
              </article>
            );
          })}
        </div>
      </div>

      <div className={styles.controls}>
        <button onClick={prev}>❮</button>
        <div className={styles.dots}>
          {slides.map((_, i) => (
            <span
              key={i}
              className={i === current ? styles.activeDot : styles.dot}
              onClick={() => goto(i)}
            />
          ))}
        </div>
        <button onClick={next}>❯</button>
      </div>
    </section>
  );
}
