"use client";

import styles from "../OrganizerCarousel/OrganizerCarousel.module.css";

export default function OrganizersCarousel({ onOpenLeaderboard }) {
  const ORGANIZERS = [
    {
      id: 1,
      name: "Swaram Academy",
      avatar: "https://png.pngtree.com/element_pic/00/16/07/06577d261edb9ec.jpg",
      events: 17,
    },
    {
      id: 2,
      name: "Noise & Grains",
      avatar: "https://static.vecteezy.com/system/resources/thumbnails/008/214/517/small_2x/abstract-geometric-logo-or-infinity-line-logo-for-your-company-free-vector.jpg",
      events: 1725,
    },
    {
      id: 3,
      name: "Amrita Vidyalayam",
      avatar: "https://img.freepik.com/free-vector/abstract-logo-flame-shape_1043-44.jpg",
      events: 3025,
    },
    {
      id: 4,
      name: "Chetri Club",
      avatar: "https://marketplace.canva.com/EAF7cca_2pA/1/0/1600w/canva-black-and-blue-initials-creative-logo-U-z4x7ZUjjs.jpg",
      events: 25,
    },
    {
      id: 5,
      name: "Microsoft",
      avatar: "https://img.freepik.com/free-vector/abstract-company-logo_53876-120501.jpg",
      events: 2300,
    },
  ];

  return (
    <section className={styles.topOrganizersroot}>
      {/* Header */}
      <div className={styles.header}>
        <div>
          <h2 className={styles.title}>Our Top Organizers</h2>
          <p className={styles.sub}>
            Find the Organizations you're looking for quickly.
            <span className={styles.more}> You can see more.</span>
          </p>
        </div>

        <button className={styles.leaderboardBtn} onClick={onOpenLeaderboard}>
          View Leaderboard &gt;&gt;&gt;
        </button>
      </div>

      {/* Organizer Cards */}
      <div className={styles.row}>
        <div className={styles.list}>
          {ORGANIZERS.map((org) => (
            <div className={styles.card} key={org.id}>
              <div className={styles.avatarWrap}>
                <img src={org.avatar} alt={org.name} className={styles.avatar} />
              </div>

              <div className={styles.name}>{org.name}</div>
              <div className={styles.events}>{org.events} events</div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
